.Color {
    grid-column: auto / span 3;
    height: 18vmax;
    min-height: 200px;    
    grid-gap: 0.2em !important;
    min-width: 320px;
    max-width: 20em;
}

.Color .color__colorpicker {
    display: none;
}

.color {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-line-pack: end;
        align-content: flex-end;
    padding: 0.4em;
    position: relative;
}

.color--value {
    grid-column: 1 / 5;
    grid-row: 2 / 4;
}

.color--contrast {
    grid-column: 5 / 9;
    grid-row: 2 / 4;
}

.color--dark {
    grid-column: 9 / end;
    grid-row: 2 / 3;
}

.color--light {
    grid-column: 9 / end;
    grid-row: 3 / 4;
}


.color__name {
    grid-row: header;
    grid-column: 1 / end;
}

.color__warning {
    position: absolute;
    bottom: 0;
    right: 0.5em;
    left: 0.5em;
    padding: 0.2em 0.5em;

    font-size: 0.5em;
    background-color: white;
    opacity: 0.8;
    color: red;
}



